import { CheckOutlined, EditOutlined, HighlightOutlined, MenuOutlined, UserOutlined } from '@ant-design/icons';
import { Avatar, Button, ButtonProps, Col, Divider, Drawer, DrawerProps, FloatButton, Row } from 'antd';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router';


interface Scoreboard {
  id: number
  date: string,
  summary: string,
  players: string[],
  state: 'completed' | 'ongoing',
  lines: Scoreline[],
}

interface Scoreline {
  lineId: number,
  scores: number[],
}



export function ScoreboardList() {
  const [open, setOpen] = useState(false);

  const buttonProps: ButtonProps = {
    type: 'primary',
    onClick: () => setOpen(true),
  }
  const drawerProps: DrawerProps = {
    title: 'Basic Drawer',
    open: open, placement: 'left', width: '75%',
    onClose: () => setOpen(false),
  }

  let scoreboards: Scoreboard[] = [
    {
      id: 1,
      date: 'March 20',
      summary: 'Hung 15 | Hung 15 | Hung 15 | Hung 15',
      players: ['Hung', 'Thach', 'Phuong', 'Vu'],
      state: 'completed',
      lines: [
        { lineId: 1, scores: [1, 2, 3, 4] },
        { lineId: 2, scores: [3, 2, 1, 4] },
        { lineId: 3, scores: [1, 5, 2, 4] },
        { lineId: 4, scores: [1, 6, 3, 4] },
      ]
    },
    {
      id: 2,
      date: 'April 4',
      summary: 'Vu 20 | Vu 20 | Vu 20 | Vu 20',
      players: ['Hung', 'Thach', 'Phuong', 'Vu'],
      state: 'ongoing',
      lines: [
        { lineId: 1, scores: [1, 2, 3, 4] },
        { lineId: 2, scores: [3, 2, 1, 4] },
        { lineId: 3, scores: [1, 5, 2, 4] },
        { lineId: 4, scores: [1, 6, 3, 4] },
      ]
    },
  ]





  return (
    <div id='SCOREBOARDLIST' className=''>
      <Row id='SCOREBOARDLIST-NAV' className='hheader h-12 w-full bg-slate-500'>

        <Button {...buttonProps} className='h-12 w-12'>
          <MenuOutlined />
        </Button>
        <Col flex='auto' className='flex'>
          <div className='text-white text-lg my-auto mx-4 font-semibold'>Scoreboard list</div>
        </Col>
      </Row>

      <Drawer id='SCOREBOARDLIST-NAV-DRAWER' {...drawerProps}>
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </Drawer>



      {scoreboards.map((b, i) => (
        <Link to={b.id.toString()} id={`MATCH_${b.id}`} key={b.id}>
          <Row className='hover:bg-slate-400'>

            <Col className='p-4'>
              {b.state === 'completed' ? <Avatar size='large' className='bg-green-500' icon={<CheckOutlined />} /> : null}
              {b.state === 'ongoing' ? <Avatar size='large' className='bg-yellow-500' icon={<HighlightOutlined />} /> : null}
            </Col>

            <Col flex='auto' className={'flex flex-col justify-center' + (i < scoreboards.length - 1 ? ' border-b border-slate-400' : '')}>
              <Row>[{b.date}]</Row>
              <Row>{b.summary}</Row>
            </Col>

          </Row>
        </Link>
      ))}

      <FloatButton icon={<EditOutlined />} />
    </div>
  );
}
